@import "reset";
@import "mixins";
@import "fonts";
@import "vars";
@import "helpers";

body {
  background-color: $light-cyan; }

.header {
  &__user-card-wrapper {
    background: {
      image: url("../img/header-bg.jpg");
      position: center 0;
      size: cover; } }

  &__nav-wrapper {
    background-color: #fff; }

  &__user-card {
    margin: 20px 0; }

  &__nav-wrapper {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $light-grey; } } }

.user-card {
  &__img {
    position: relative;
    border-radius: 50%;
    box-shadow: 0 0 5px 2px rgba(0,0,0,0.2);
    z-index: 1; }

  &__info {
    left: 100px;
    padding: {
      left: 140px;
      right: 20px;
      top: 16px;
      bottom: 16px; }
    margin-bottom: 10px;
    background-color: #ffffff;
    border-radius: 50px; }

  &__title {
    width: 100%;
    word-wrap: normal;
    color: $blue;
    font-family: 'Raleway Bold', sans-serif;
    font-weight: 700; }

  &__topics {
    margin-top: 8px; } }

.topics {
  &__item {
    position: relative;
    padding-right: 20px;
    &:not(:first-child) {
      padding-left: 20px;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        height: 20px;
        width: 1px;
        background-color: #969696; } } }

  &__icon {
    color: #969696;
    @include sizes(16px); }

  &__title {
    color: #969696;
    word-wrap: normal;
    font-size: rem(16px);
    margin: {
      top: 2px;
      left: 8px; } } }

.nav {
  &__item {
    position: relative;
    display: block;
    color: $blue;
    text-decoration: none;
    padding: {
      left: 80px;
      right: 80px;
      top: 40px;
      bottom: 32px; }
    font-size: rem(36px);
    font-family: 'Bebas Neue Regular', sans-serif;
    transition: all 0.4s;
    &:after {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: $cyan;
      z-index: 1;
      opacity: 0;
      transition: all 0.4s; }
    &:hover {
      background-color: $light-grey;
      transition: all 0.4s;
      &:after {
        opacity: 1;
        transition: all 0.4s; } } } }


.cards {
  padding: {
    top: 50px;
    bottom: 60px; }

  &__columns {
    display: flex;
    justify-content: space-between;
    width: 100%; }

  &__col {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: calc(50% - 40px); }

  &__item {
    margin: 10px 0;
    display: block;
    width: 100%; } }

.card {
  position: relative;
  padding: 30px 25px;
  background-color: #ffffff;
  color: $blue;
  border-radius: 15px;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
  overflow: hidden;

  &__title {
    font: {
      size: rem(30px);
      family: 'Bebas Neue Regular', sans-serif;
      weight: normal; }
    text-transform: uppercase; }

  &__subtitle {
    font-weight: bold; }

  &__photos {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }

  &__link {
    color: $cyan;
    text-decoration: none;
    &:hover {
      text-decoration: underline; } }

  &__photo {
    @include sizes(rem(60));

    &--empty {
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
      border-radius: 3px; } }

  &__monobrow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: block;
    height: 5px;
    border-top: {
      left-radius: 15px;
      right-radius: 15px; }

    &--blue {
      background-color: #5fc0e7; }

    &--purple {
      background-color: #7c4098; }

    &--red {
      background-color: #fa5770; }

    &--orange {
      background-color: #f26632; } } }


.gallery {
  &__tabs {
    padding: 15px 35px; }

  &__images {
    padding: 15px; } }
.tabs {
  background-color: #ffffff;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
  color: $blue;
  border-radius: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;

  &__title {
    font-size: rem(18);
    font-family: 'Raleway Bold', sans-serif; }

  &__list {
    display: flex;
    flex-direction: row;
    margin-left: 30px;
    flex-wrap: wrap; }

  &__item {
    display: block;
    padding: {
      top: 10px;
      right: 20px;
      bottom: 8px;
      left: 20px; }
    font-size: rem(16);
    border-radius: 50px;
    text-transform: uppercase;
    color: $blue;
    background: none;
    border: none;
    cursor: pointer;

    &.active {
      background-color: $cyan;
      color: #ffffff; }

    &:hover {
      background-color: $cyan;
      color: #ffffff;
      transition: all 0.4s; } }

  &__item-wrapper:not(:last-child) {
    margin-right: 34px; } }


.images {
  background-color: #fff;
  box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.1);
  color: $blue;
  width: 100%;

  &__container {
    display: flex;
    flex-wrap: wrap; }

  &__item {
    position: relative;
    width: calc(25% - 20px);
    cursor: pointer;
    padding: 0;
    border-radius: 3px;
    overflow: hidden;
    margin: 10px;
    &:hover {
      .images__overlay {
        opacity: 1;
        transition: all 0.4s; } } }

  &__overlay {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: block;
    background-color: rgba(0, 43, 65, 0.8);
    opacity: 0;
    transition: all 0.4s;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: block;
      background: url(../img/icons/loupe.png) no-repeat center center; } }
  &__img {
    width: 100%;
    height: 100%; } }

.popup {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  &__overlay {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: lightblue;
    z-index: 9999;
    background-color: rgba(0, 43, 65, 0.8); }

  &__img-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
    padding: 0; }


  &__close {
    position: absolute;
    right: -27px;
    top: -27px;
    width: 54px;
    height: 54px;
    background-color: $cyan;
    border-radius: 50%;
    border: none; }

  &__img {
    max-width: 500px;
    max-height: calc(100vh - 100px);
    width: auto; } }


@import "media"; // Always in the END
